import PointEditView from '../views/point-edit';
import { render, remove } from '../framework/render';
import { NEW_BLANK_POINT } from '../point-mock';
import { nanoid } from 'nanoid';
export default class NewPointPresenter {
    #pointContainer = null;
    #pointEditComponent = null;
    #newPoint = NEW_BLANK_POINT;
    #handleDataChange = null;
    #handleDestroy;
    constructor({ pointContainer, onDataChange, onDestroy }) {
        this.#pointContainer = pointContainer;
        this.#handleDataChange = onDataChange;
        this.#handleDestroy = onDestroy;
    }
    init() {
        if (this.#pointEditComponent !== null) {
            return;
        }
        this.#pointEditComponent = new PointEditView({
            point: this.#newPoint,
            onFormSubmit: this.#handleFormSubmit,
            onDeleteClick: this.#handleDeleteClick,
        });
        render(this.#pointEditComponent, this.#pointContainer, 'afterbegin');
        document.addEventListener('keydown', this.#escKeyDownHandler);
    }
    destroy() {
        if (this.#pointEditComponent === null) {
            return;
        }
        this.#handleDestroy();
        remove(this.#pointEditComponent);
        this.#pointEditComponent = null;
        document.removeEventListener('keydown', this.#escKeyDownHandler);
    }
    #handleFormSubmit = (point) => {
        this.#handleDataChange('ADD_POINT', 'MINOR', { id: nanoid(), ...point });
        this.destroy();
    };
    #handleDeleteClick = () => {
        this.destroy();
    };
    #escKeyDownHandler = (evt) => {
        if (evt.key === 'Escape' || evt.key === 'Esc') {
            evt.preventDefault();
            this.destroy();
        }
    };
}
